import { useEffect, useState } from "react";

import classes from "./PackagesTypes.module.css";

const packagesType = [
  { id: 1, title: "Logo Design" },
  { id: 2, title: "Website Design" },
  { id: 3, title: "E-Commerce" },
  { id: 4, title: "SEO Packages" },
  { id: 5, title: "Video Animation" },
  { id: 6, title: "Branding" },
];

const PackagesTypes = (props) => {
  const [types, setType] = useState({
    activeObject: packagesType[0],
    objects: packagesType,
  });
  const activeBtnHandler = (index) => {
    setType({ ...types, activeObject: types.objects[index] });
    props.onSelection(types.objects[index]);
  };
  const toggleActiveClass = (index) => {
    if (types.objects[index] === types.activeObject) {
      return `${classes.btn} ${classes.currentBtn}`;
    } else {
      return `${classes.btn}`;
    }
  };

  return (
    <>
      <div className="container-lg">
        <div
          className={`${
            classes.Container
          } ${"d-flex flex-row text-align-center justify-content-center pb-4"}`}
        >
          {types.objects.map((type, index) => (
            <div className="pe-3 pb-2">
              <button
                key={index}
                className={toggleActiveClass(index)}
                onClick={() => {
                  activeBtnHandler(index);
                }}
              >
                {type.title}
              </button>
            </div>
          ))
          }
        </div>
      </div>
    </>
  );
};

export default PackagesTypes;
